<template>
	<v-container>
		<v-row justify="space-around" class="pt-1">
			<v-col cols="4">
				<v-card class="mx-auto pa-5" outlined>
					<div class="mb-3">
						<v-text-field label="API Key" v-model="apiKey" />
						<v-text-field label="Service Code" v-model="serviceCode" />
					</div>

					<v-btn tile color="success" @click="getNumberPhoneAPI">
						<v-icon dark class="mr-5"> mdi-cloud-upload </v-icon>
						Request Number Phone
					</v-btn>
				</v-card>
			</v-col>

			<v-col cols="8">
				<v-data-table :headers="headers" :items="desserts" class="elevation-1">
					<template v-slot:top>
						<v-toolbar flat>
							<v-toolbar-title>Mèo Méo Meo</v-toolbar-title>
							<v-divider class="mx-4" inset vertical></v-divider>
							<v-spacer></v-spacer>

							<v-btn color="primary" class="mb-2" @click="reloadAllOTP">
								Reload All OTP
							</v-btn>

							<v-dialog v-model="dialogDelete" max-width="500px">
								<v-card>
									<v-card-title class="text-h5"
										>Are you sure you want to delete this item?</v-card-title
									>
									<v-card-actions>
										<v-spacer></v-spacer>
										<v-btn color="blue darken-1" text @click="closeDelete"
											>Cancel</v-btn
										>
										<v-btn
											color="blue darken-1"
											text
											@click="deleteNumberPhoneConfirm"
											>OK</v-btn
										>
										<v-spacer></v-spacer>
									</v-card-actions>
								</v-card>
							</v-dialog>
						</v-toolbar>
					</template>

					<template v-slot:item.actions="{ item }">
						<v-icon small class="mr-2" @click="getOtpAPI(item)">
							mdi-autorenew
						</v-icon>
						<v-icon small @click="deleteNumberPhone(item)">
							mdi-delete-alert-outline
						</v-icon>
					</template>
				</v-data-table>
			</v-col>
		</v-row>
    <v-dialog
      v-model="showError"
      persistent
      max-width="290"
    >
      <v-card>
        <v-alert
          shaped
          type="error"
          max-width="300"
        >
          Hết số rồi bạn ơi!
        </v-alert>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="red darken-1"
            text
            @click="closeErrorAlert"
          >
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
	</v-container>
</template>

<script>
import axios from "axios";
export default {
	data() {
		return {
			dialog: false,
			dialogDelete: false,
      showError: false,
			headers: [
				{
					text: "ID",
					align: "start",
					sortable: false,
					value: "order_id"
				},
				{ text: "Phone Number", value: "phonenumber" },
				{ text: "OTP", value: "otp" },
				{ text: "Actions", value: "actions", sortable: false }
			],
			desserts: [],
			editedIndex: -1,
			editedItem: {
				order_id: "",
				phonenumber: 0,
				otp: 0
			},
			defaultItem: {
				order_id: "",
				phonenumber: 0,
				otp: 0
			},
			serviceCode: "facebook_us",
			apiKey: "edf246edaa24e53ea5676910c34451cbf50b2564"
		};
	},
	watch: {
		dialog(val) {
			val || this.close();
		},
		dialogDelete(val) {
			val || this.closeDelete();
		}
	},
	created() {
		this.initialize();
	},
	methods: {
		getNumberPhoneAPI() {
			axios
				.post(
					`https://tempcode.co/api/orders.php?api_key=${
						this.apiKey
					}&act=buy_number&service_id=${this.serviceCode}`
				)
				.then(response => {
					if(response.data.success) {
            response.data.map(item => {
              this.desserts.push({
                order_id: item.order_id,
                phonenumber: item.phonenumber
              });
            });
          } else {
            this.openErrorAlert()
          }
				})
				.catch(e => {
					this.errors.push(e);
				});
			// this.desserts.push({
			//   order_id: Math.random(),
			//   phonenumber: Math.random()
			// })
		},
		initialize() {
			this.desserts = [];
		},
		getOtpAPI(item) {
			axios
				.post(
					`https://tempcode.co/api/orders.php?api_key=${
						this.apiKey
					}&act=read_message&order_id=${item.order_id}`
				)
				.then(response => {
					if (response.data.success) {
						const otp = response.data.data.otp;
						this.editedIndex = this.desserts.indexOf(item);
						this.editedItem = Object.assign({}, item);
						this.editedItem.otp = otp;
						Object.assign(this.desserts[this.editedIndex], this.editedItem);
					}
				})
				.catch(e => {
					this.errors.push(e);
				});
		},
		deleteNumberPhone(item) {
			this.editedIndex = this.desserts.indexOf(item);
			this.editedItem = Object.assign({}, item);
			this.dialogDelete = true;
		},
		deleteNumberPhoneConfirm() {
			this.desserts.splice(this.editedIndex, 1);
			this.closeDelete();
		},
		closeDelete() {
			this.dialogDelete = false;
			this.$nextTick(() => {
				this.editedItem = Object.assign({}, this.defaultItem);
				this.editedIndex = -1;
			});
		},
		reloadAllOTP() {
			this.desserts.map(item => this.getOtpAPI(item));
		},
    closeErrorAlert() {
      this.showError = false;
    },
    openErrorAlert() {
      this.showError = true;
    }
	}
};
</script>
